
export default {
  name: 'featuresItem',
  props: {
    title: String,
    text: String,
    img: null,
    link: String
  },
  setup(props: any) {
    let featuresObj = {
      title: props.title,
      text: props.text,
      img: props.img,
      link: props.link
    }
    // console.log(featuresObj.link)

    return { featuresObj }
  }
}
