
// 导入打字机特效
import EasyTyper from 'easy-typer-js'
import { reactive } from 'vue'

export default {
  name: 'parallax',
  props: {
    // 标题
    title: {
      type: String,
      required: true
    },
    // 描述
    description: {
      type: String,
      required: true
    },
    // 背景图片
    bg: {
      type: String
    }
  },
  setup({ description }: any) {
    let TyperObj = reactive({
      output: '',
      isEnd: false,
      speed: 150,
      singleBack: false,
      sleep: 1000,
      type: 'rollback',
      backSpeed: 50,
      sentencePause: false
    })

    // 分割字符串
    if (description.length >= 40) {
      description = description.slice(0, 40)

      // 字符串末尾
      let lastStr = description.slice(
        description.length - 1,
        description.length
      )

      // 判断末尾
      if (lastStr != '？' || lastStr != '。' || lastStr != '！') {
        // 添加省略号Î
        description += '...'
      }
    }

    new EasyTyper(
      TyperObj,
      [],
      (instance: any) => {
        // 回调函数 如果这里使用了递归调用会一直循环打印，需要在外部触发停止
        // 此回调用于获取新的数据然后重新输出
        instance.input = [description]
        instance.play()
      },
      function () {}
    )

    return { TyperObj }
  }
}
